<template>
    <div>
        <beer-provider :detailed=true :id="id">
            <template slot-scope="{ beer }">
              <div id="picture" v-background="beer.image_url" />
                <h3>{{ beer.name }}</h3>
                <h4>{{ beer.tagline }}</h4>
                <p>
                  {{ beer.description }}
                </p>
                <button @click="showModal = true" >
                  check in
                </button>
                <div class="details">
                  <h4>Facts</h4>
                  <ul>
                    <li>Average rating: {{ beer.average }} - </li>
                    <li>IBU: {{ beer.ibu }} - </li>
                    <li>ABV: {{ beer.abv }}</li>
                  </ul>
                </div>
                <div v-if="beer.check_ins" class="lastUsers">
                  <h4>Last checked by</h4>
                  <ul>
                    <li :key="checkin.id" v-for="checkin in beer.check_ins" >
                      <checkin-item :checkin="checkin" />
                    </li>
                  </ul>
                </div>

                <modal v-if="showModal"  @close="showModal = false"  @submit="checkin()" >
                  <h3 slot="header">You're going to check in to {{beer.name}}</h3>
                  <div slot="body">
                    <input type="textarea" v-model="text" />
                    <star-rating :star-size="25" :increment=".25" v-model="rating" />
                  </div>
                </modal>

            </template>
        </beer-provider>
    </div>
</template>

<script>
import BeerProvider from '../providers/BeerProvider.vue'
import Modal from '../containers/Modal'
import StarRating from 'vue-star-rating'
import CheckinItem from '../dumb/CheckinItem'
import { checkIn } from '../../database/queries.js'

export default {
  props: {
    id: {
      type: String,
      default: null,
      required: false,
    },
  },
  data() {
    return {
      showModal: false,
      text: '',
      rating: 0,
    }
  },
  components: {
    BeerProvider,
    StarRating,
    CheckinItem,
    Modal,
  },
  methods: {
    async checkin() {
      const info = {
        beer: this.id,
        rating: this.rating,
        text: this.text,
      }

      try {
        await this.$apollo.mutate({
          mutation: checkIn,
          variables: info,
        })

        this.showModal = false
        this.rating = 0
        this.text = ''
      } catch (err) {
        console.warn(err)
      }
    },
  },
}
</script>

<style scoped>
li {
  display: inline-block;
  vertical-align: top;
}
</style>
